
  import { defineComponent} from 'vue';
  import Temporize from './Temporize.vue';

  export default defineComponent({
    name: "FormsCreated",
    emits: ['saveTask'],
    data(){
      return {
        descricao: ''
      }
    },
    methods: {
      finishTodo(timeStarted: number) : void{
        this.$emit('saveTask', {
          duration: timeStarted,
          description: this.descricao
        })
        this.descricao = ''
      }
    },
    components: { Temporize }
})
